}

.chat-list, .chat-list__header, .chat-list .chat-search, .chat-list__items {
  width: 310px;
}

.chat-list__header {
  justify-content: space-between;
  margin-bottom: 1.25rem;
  padding: 0 .75rem;
  display: flex;
}

.chat-list__logo {
  width: 87px;
  margin-top: -.25rem;
}

.chat-list__items {
  padding-left: .75rem;
  padding-right: .75rem;
}

.chat-list__container {
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}

.chat-list__item {
  height: 5rem;
  --tw-border-opacity: 1;
  border-bottom-width: 1px;
  border-bottom-color: rgb(234 236 240 / var(--tw-border-opacity));
}

.chat-list__item:hover {
  background-image: linear-gradient(to right, var(--tw-gradient-stops));
  --tw-gradient-from: #fff;
  --tw-gradient-to: #fff0;
  --tw-gradient-stops: var(--tw-gradient-from), var(--tw-gradient-to);
  --tw-gradient-to: #e2f5ff00;
  --tw-gradient-stops: var(--tw-gradient-from), #e2f5ff, var(--tw-gradient-to);
  --tw-gradient-to: #fff;
}

.chat-list__item.selected {
  --tw-bg-opacity: 1;
  background-color: rgb(226 245 255 / var(--tw-bg-opacity));
}

.chat-list__profile-button {
  align-items: center;
  display: flex;
}

.chat-list__profile-button .caption {
  --tw-text-opacity: 1;
  color: rgb(254 147 140 / var(--tw-text-opacity));
  margin-right: .5rem;
  font-size: .75rem;
  font-weight: 700;
  line-height: 1rem;
  display: none;
}

.chat-list__profile-button:hover .caption {
  display: block;
}

.chat-list__profile-button .btn {
  height: 2rem;
  width: 2rem;
  cursor: pointer;
  background-image: url("Profile_icon.cc94b498.svg");
  background-repeat: no-repeat;
  background-size: contain;
}

.chat-list-collapsed {
  width: 60px;
  cursor: pointer;
  position: relative;
}

