  background-image: url("Expand-icon.a666c9dd.svg");
  background-position: center;
  background-repeat: no-repeat;
  position: absolute;
  top: 50%;
  left: 50%;
}

.chat-search {
  padding-left: .75rem;
  padding-right: .75rem;
  position: relative;
}

.chat-search__button {
  height: 1rem;
  width: 1rem;
  background-image: url("Search-icon.d0925095.svg");
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  bottom: 2rem;
  right: .75rem;
}

.chat-list-item__link {
  height: 100%;
  width: 100%;
  align-items: center;
  padding: .75rem;
  display: flex;
  position: relative;
}

.chat-list-item__avatar {
  height: 3.5rem;
  width: 3.5rem;
  border-radius: 9999px;
  margin-right: .75rem;
}

.chat-list-item__avatar-noimage {
  background-image: linear-gradient(to bottom, var(--tw-gradient-stops));
  --tw-gradient-from: #c1e0f0;
  --tw-gradient-to: #c1e0f000;
  --tw-gradient-stops: var(--tw-gradient-from), var(--tw-gradient-to);
  --tw-gradient-to: #81bfdb;
}

.chat-list-item__text {
  flex-grow: 1;
  margin: 0;
}

.chat-list-item__text strong {
  --tw-text-opacity: 1;
  color: rgb(42 71 91 / var(--tw-text-opacity));
}

.chat-list-item__title {
  --tw-text-opacity: 1;
  color: rgb(42 71 91 / var(--tw-text-opacity));
  margin-bottom: .25rem;
  font-size: .875rem;
  line-height: 1.25rem;
}

.chat-list-item__recent {
  --tw-text-opacity: 1;
  color: rgb(102 112 133 / var(--tw-text-opacity));
  font-size: .75rem;
  line-height: 1rem;
}

.chat-list-item__info {
  height: 100%;
  flex-direction: column;
  flex-shrink: 0;
  justify-content: space-between;
  align-items: flex-end;
  display: flex;
}

.chat-list-item__unread {
  height: 1.25rem;
  width: 1.25rem;
  --tw-bg-opacity: 1;
  background-color: rgb(254 147 140 / var(--tw-bg-opacity));
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity));
  border-radius: 9999px;
  justify-content: center;
  align-items: center;
  font-size: .75rem;
  font-weight: 700;
  line-height: 1rem;
  display: flex;
}

.chat-list-item__time {
  --tw-text-opacity: 1;
  color: rgb(42 71 91 / var(--tw-text-opacity));
  font-size: .7rem;
}

.chat-select-notification {
  color: #fff;
  text-align: center;
  margin: 50px;
  font-weight: bold;
}

.error {
  --tw-border-opacity: 1;
  border-width: 2px;
  border-color: rgb(255 255 255 / var(--tw-border-opacity));
  text-align: center;
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity));
  border-radius: 1.5rem;
  padding: 2.5rem 5rem 5rem;
  position: relative;
}

.error-code {
  font-size: 10rem;
  font-weight: 700;
}

.error-message {
  font-size: 1.25rem;
  line-height: 1.75rem;
}

.error .button {
  --tw-translate-x: -50%;
  transform: translate(var(--tw-translate-x), var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
  --tw-translate-y: 50%;
  transform: translate(var(--tw-translate-x), var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
  position: absolute;
  bottom: 0;
  left: 50%;
}

.inputfield {
  flex-direction: column-reverse;
  margin-bottom: .5rem;
  display: flex;
}

.inputfield__label, .inputfield__input {
  transition-property: transform;
  transition-duration: .2s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.inputfield__label {
  --tw-text-opacity: 1;
  color: rgb(183 183 183 / var(--tw-text-opacity));
  margin-bottom: 5px;
  font-size: .7rem;
  position: relative;
}

.inputfield__input {
  --tw-text-opacity: 1;
  color: rgb(42 71 91 / var(--tw-text-opacity));
  --tw-border-opacity: 1;
  border-bottom-width: 1px;
  border-bottom-color: rgb(183 183 183 / var(--tw-border-opacity));
  outline-offset: 2px;
  outline: 2px solid #0000;
  margin-top: -5px;
  padding: 0;
  font-size: 20px;
  line-height: 28px;
  display: block;
}

.inputfield__input.error {
  --tw-border-opacity: 1;
  border-bottom-width: 1px;
  border-bottom-color: rgb(208 78 23 / var(--tw-border-opacity));
}

.inputfield__input:focus {
  --tw-border-opacity: 1;
  border-bottom-color: rgb(42 71 91 / var(--tw-border-opacity));
  outline-offset: 2px;
  outline: 2px solid #0000;
}

.inputfield__input:placeholder-shown + label {
  pointer-events: none;
  transform-origin: 0 100%;
  transform: translate(0, 1.75rem)scale(1.81818);
}

.inputfield__input:not(:placeholder-shown) + label, .inputfield__input:focus + label {
  cursor: default;
  transform: translate(0)scale(1);
}

.inputfield__input:disabled {
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  --tw-text-opacity: 1;
  color: rgb(183 183 183 / var(--tw-text-opacity));
  opacity: .7;
}

.inputfield__input:disabled + label {
  cursor: default;
}

.inputfield__message {
  height: 12px;
  --tw-text-opacity: 1;
  color: rgb(208 78 23 / var(--tw-text-opacity));
  margin-top: 2px;
  font-size: 12px;
}

::-webkit-input-placeholder {
  opacity: 0;
  transition: inherit;
}

.form-profile {
  padding: 2.125rem 2rem 2rem 4rem;
  display: flex;
  position: relative;
}

.form-profile .inputfield {
  width: 18rem;
}

.form-profile .column.column1 {
  margin-right: 2rem;
  padding-top: 2.5rem;
}

.form-profile .column.column2 .button {
  margin-top: 1.5rem;
}

.form-profile .link-block {
  margin-top: 3rem;
}

.form-profile__avatar {
  cursor: pointer;
  position: absolute;
  top: -1.5rem;
  left: -3.8125rem;
}

.form-profile__avatar .img {
  height: 7.625rem;
  width: 7.625rem;
  border-radius: 9999px;
}

.form-profile__avatar .icon {
  height: 2.75rem;
  width: 2.75rem;
  background-image: linear-gradient(to bottom, var(--tw-gradient-stops));
  --tw-gradient-from: #efa5a0;
  --tw-gradient-to: #efa5a000;
  --tw-gradient-stops: var(--tw-gradient-from), var(--tw-gradient-to);
  --tw-gradient-to: #fe938c;
  border-radius: 9999px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  bottom: 0;
  right: 0;
}

.form-profile .btn-logout {
  height: 1.25rem;
  width: 1.25rem;
  background-image: url("Logout-icon.e1fc7e39.svg");
  background-size: contain;
  position: absolute;
  top: 1rem;
  right: 1rem;
